import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { Button, Nav, Form, Row, Col } from "react-bootstrap";
import { listProductCategories } from '../store/actions/categoriesActions'
import { myDetails } from '../store/actions/userActions'
import { createStore } from "../store/actions/storeActions";
import Map, { Marker, Popup } from "react-map-gl";
import AddProductScreen from '../screens/AddProductScreen'
import { STORE_CREATE_RESET } from '../store/constants/storeConstants'
import styled from "styled-components";
import useInput from "./useInput";
import context from "react-bootstrap/esm/AccordionContext";
import FormContainer from "../components/FormContainer";

export default function AddStoreScreen() {

  const [viewState, setViewState] = useState({
    latitude: 46.738436, 
    longitude: 8.082641,
    zoom: 7
  });

  // const [selectedPark, setSelectedPark] = useState(null)
  // const stores = useSelector( state => state.storesReducer.stores)

  const [state, setState] = useState({
    owner: '',
    name: '',
    address:'',
    country:'',
    city:'',
    latitude: '',
    longitude: '',
    description: '',
    phone: '',
    image: '',
    category: []
  });

  const address = useInput("");

  const [ select, setSelect] = useState('')
  const [ name, setName ] = useState('')

  const dispatch = useDispatch()
  const navigate = useNavigate()

  const { categories } = useSelector(state => state.productCategories)
  const { user } = useSelector(state => state.myDetails)
  const { success: createStoreSuccess } = useSelector(state => state.createStore)

  useEffect(() => {
    if (createStoreSuccess) {
      navigate('/mystores')
      dispatch({ type: STORE_CREATE_RESET })
    }
    dispatch(listProductCategories())    
    dispatch(myDetails())
  }, [dispatch, createStoreSuccess])

  const onChange = ({ target: { name, value } }) => {
    setState({...state, [name]: value})
  };

  const handleCategoryChange = e => {
    var options = e.target.options;
    var category = [];
    for (var i = 0, l = options.length; i < l; i++) {
      if (options[i].selected) {
        category.push(options[i].value);
      }
    }
    setState({...state, category: category})
    console.log('addstorecat', category)
  };

  const handleImageChange = e => {
    const image = e.target.files[0];
    setState({...state, image: image})
  };

  const handleSubmit = async e => {
    e.preventDefault();
    let form_data = new FormData();

    for (var i = 0; i < state.category.length; i++) {
      form_data.append("category", state.category[i]);
    }

    form_data.set('owner', user.id)
    form_data.set('name', name)
    form_data.set('address', state.address)
    form_data.set('country', state.country)
    form_data.set('city', state.city)
    form_data.set("latitude", state.latitude);
    form_data.set("longitude", state.longitude);
    form_data.set("description", state.description);
    form_data.set("phone", state.phone);
    form_data.set("image", state.image);
   
    dispatch(createStore(form_data))
  };

    return (
      <>
      <FormContainer> 
      <Row>
            <Col md="4" className="m-5" style={{width:'70%'}}>
            <h4 className="form_titles mb-4" style={{textAlign: 'center'}}>Add Store</h4>
  
            <Form className="NewRestaurant-container" onSubmit={handleSubmit}>
  
            <Form.Group className="mb-4" >
                <Form.Label>Name</Form.Label>
                <Form.Control
                  required
                  type="text"
                  name="name"
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                />
              </Form.Group>
  
            <Form.Group className="mb-4" >
              <Form.Label>Address</Form.Label>
                <Form.Control
                  placeholder="Search"
                  {...address}
                  isTyping={address.value !== ""}
                />
                  {address.suggestions?.length > 0 && (
                    <SuggestionWrapper>
                      {address.suggestions.map((suggestion, index) => {
                        return (
                          <Suggestion
                            key={index}
                            onClick={(e) => {
                              e.preventDefault()
                              const country = suggestion.context[suggestion.context.length - 1].text
                              const city = suggestion.context.find(x => x.id.split('.')[0] === 'region')
                              address.setValue(suggestion.place_name);
                              address.setSuggestions([]);
                              setState({ 
                                latitude: suggestion.geometry.coordinates[1].toFixed(6), 
                                longitude: suggestion.geometry.coordinates[0].toFixed(6),
                                country,
                                city: city?.text,
                                address: suggestion.place_name
                              })
                            }}
                          >
                            {suggestion.place_name}
                          </Suggestion>
                        );
                      })}
                  </SuggestionWrapper>
                )}
              </Form.Group>
              
              { state.latitude && (
                <Row>
                <Col style={{width:'30%'}}>
                <Form.Group className="mb-4" >
                <Form.Label>Latitude</Form.Label>
                <Form.Control
                  required
                  type="text"
                  name="latitude"
                  value={state.latitude}
                  onChange={onChange}
                />
              </Form.Group>   
              </Col> 
              <Col style={{width:'30%'}}>
              <Form.Group className="mb-4" >
                <Form.Label>Longitude</Form.Label>
                <Form.Control
                  required
                  type="text"
                  name="longitude"
                  value={state.longitude}
                  onChange={onChange}
                />
              </Form.Group>
              </Col>
              </Row>
              )}
                
              <Form.Group>
              <Form.Label>
                Description
              </Form.Label>
              <Form.Control
                as="textarea"
                name="description"
                value={state.description}
                onChange={onChange}
                rows="3"
                className="restForm-description"
              />
            </Form.Group> 
  
              <Form.Group className="mb-4" >
                <Form.Label>Phone</Form.Label>
                <Form.Control
                  type="text"
                  name="phone"
                  value={state.phone}
                  onChange={onChange}
                />
              </Form.Group> 
  
              <Form.Group className="mb-4">
                <Form.Label>Image</Form.Label>
                <div>
                  <input
                    type="file"
                    accept="image/*"
                    onChange={handleImageChange}
                  />
                </div>
              </Form.Group>
  
              <Form.Group >
              <Form.Label >
                Categories
              </Form.Label>
              <Form.Control
                as="select"
                type="text"
                name="category"
                value={state.category}
                onChange={handleCategoryChange}
                multiple
              >
                {categories &&
                  categories.map(category => {
                    return <option key={category.id}>{category.name}</option>;
                  })}
              </Form.Control>
              </Form.Group>
  
              <Button
                variant="primary"
                type="submit">
                Submit
              </Button>
            </Form>
          </Col>
      </Row>
        
      </FormContainer>
      </>
    );
  }

  const Wrapper = styled.div`
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  margin: 0 auto;
`;

const Input = styled.input`
  width: 400px;
  background: white;
  border: none;
  padding: 10px 20px;
  border-radius: 30px;
  position: relative;
  display: grid;
  justify-self: center;
  &:focus {
    outline: none;
    border-radius: ${(props) => props.isTyping && "10px 10px 0px 0px"};
  }
`;

const SuggestionWrapper = styled.div`
  background: white;
  position: absolute;
  width: 400px;
  padding: 10px 20px;
  border-radius: 0px 0px 10px 10px;
`;

const Suggestion = styled.p`
  cursor: pointer;
  max-width: 400px;
`;