import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import Map, { Marker, Popup } from "react-map-gl";
// import 'mapbox-gl/dist/mapbox-gl.css';
import { Col, Form, Row, Carousel, Image, NavLink } from 'react-bootstrap'
import '../screens/HomeScreen.css'
import { search } from '../store/actions/searchAction'
import { listStores } from '../store/actions/storeActions'
import { listProductCategories } from '../store/actions/categoriesActions'
import { listProfiles } from '../store/actions/userActions'
// import styled from "styled-components";
// import InputField from "./inputField";


function MapScreen() {

    const [ select, setSelect ] = useState(false)

  const [viewState, setViewState] = useState({
    latitude: 46.738436, 
    longitude: 8.082641,
    zoom: 8
  });

  const dispatch = useDispatch()

  const [selectedStore, setSelectedStore] = useState(null)
  const { stores } = useSelector( state => state.storeList)
  const { profiles } = useSelector(state => state.profileList)

  const { categories } = useSelector(state => state.productCategories)

  useEffect(() => {
    dispatch(listProfiles())
    dispatch(listStores())
    dispatch(listProductCategories())
  }, [dispatch])

  const storeOwner = selectedStore && profiles.find( (profile) => profile.name === selectedStore.owner_name)

  const handleShowMap = () => {
      const change = !select
      setSelect(change);
  }

  const filterOptionHandler = (event) => {
    dispatch(search({ type: 'stores', searchString: event.target.value}))
  }

  return (
    <>
    <Row style={{ backgroundColor:'#1e478a', height:'3rem' }}>
             <Form.Select className="d-flex justify-content-center my-1" onChange={filterOptionHandler} aria-label="Default select example" style={{ width: '40%', margin: 'auto'}}>
                <option >Filter by Category</option>
                    {categories.map((category) => {  
                        return <option >{category.name}</option>
                    })}
            </Form.Select>
        </Row>
    <Row >
      <Map
        {...viewState}
        mapboxAccessToken={process.env.REACT_APP_MAPBOX_TOKEN}
        mapStyle="mapbox://styles/mapbox/light-v11"
        style={{ height: "80vh"}}
        onMove={evt => setViewState(evt.viewState)}
      >
        {stores.map((store, index) => (
          <Marker
            key={index}
            latitude={store.latitude}
            longitude={store.longitude}
          >
            <button
              className="marker-btn"
              onClick={e => {
                e.preventDefault();
                setSelectedStore(store);
              }}
            >
              <i className="bi bi-geo-alt-fill"></i>
            </button>
          </Marker>
        ))}

        {selectedStore ? (
          <Popup
            latitude={selectedStore.latitude}
            longitude={selectedStore.longitude}
            closeOnClick={false} 
            onClose={() => {
              setSelectedStore(null);
            }}
          >
            <img src={selectedStore.image} alt="Store Icon" style={{ width:'10vw'}}/>
            <strong><p>{selectedStore.name}</p></strong>
            <p><strong>Address: </strong>{selectedStore.address}</p>
            <strong><p>Go to <a href={`/retailers/${storeOwner.id}`}>{selectedStore.owner_name}</a>'s page</p></strong>
          </Popup>
        ) : null } 
      </Map>
      </Row>
      </>
  )
}

export default MapScreen