import { GET_USER_PROFILE, CLEAR_USER_PROFILE } from "../types";

const initialState = {
  userProfile: {}
};

export const userProfileReducer = (state = initialState, action) => {

  switch (action.type) {
    case GET_USER_PROFILE: {
      return { ...state, userProfile: action.payload };
    }
    case CLEAR_USER_PROFILE:
      return { userProfile: {} };
    default:
      return state;
  }
};
