import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { Button, Nav, Form, Row, Col } from "react-bootstrap";
import { listProductCategories } from '../store/actions/categoriesActions'
import { myDetails } from '../store/actions/userActions'
import { createStore } from "../store/actions/storeActions";
import Map, { Marker, Popup } from "react-map-gl";
import AddProductScreen from '../screens/AddProductScreen'

import styled from "styled-components";
import useInput from "../screens/useInput";
import context from "react-bootstrap/esm/AccordionContext";
import FormContainer from "../components/FormContainer";

export default function CreateStore() {

  const [viewState, setViewState] = useState({
    latitude: 46.738436, 
    longitude: 8.082641,
    zoom: 7
  });

  // const [selectedPark, setSelectedPark] = useState(null)
  // const stores = useSelector( state => state.storesReducer.stores)

  const [state, setState] = useState({
    owner: '',
    name: '',
    address:'',
    country:'',
    city:'',
    latitude: '',
    longitude: '',
    description: '',
    phone: '',
    image: '',
    category: []
  });

  const address = useInput("");

  const [ select, setSelect] = useState(false)
  const [ name, setName ] = useState('')

  const dispatch = useDispatch()
  const navigate = useNavigate()

  const { categories } = useSelector(state => state.productCategories)
  const { user } = useSelector(state => state.myDetails)

  useEffect(() => {
    dispatch(listProductCategories())    
    dispatch(myDetails())
  }, [dispatch])

  const onChange = ({ target: { name, value } }) => {
    setState({...state, [name]: value})
  };

  const handleCategoryChange = e => {
    var options = e.target.options;
    var category = [];
    for (var i = 0, l = options.length; i < l; i++) {
      if (options[i].selected) {
        category.push(options[i].value);
      }
    }
    setState({...state, category: category})
  };

  const handleImageChange = e => {
    const image = e.target.files[0];
    setState({...state, image: image})
  };

  const handleSubmit = async e => {
    e.preventDefault();
    let form_data = new FormData();
    // if (state.image) {
    //   form_data.append("image", state.image, state.image.name);
    // }
    
    for (var i = 0; i < state.category.length; i++) {
      form_data.append("category", state.category[i]);
    }

    form_data.set('owner', user.id)
    form_data.set('name', name)
    form_data.set('address', state.address)
    form_data.set('country', state.country)
    form_data.set('city', state.city)
    form_data.set("latitude", state.latitude);
    form_data.set("longitude", state.longitude);
    form_data.set("description", state.description);
    form_data.set("phone", state.phone);
    form_data.set("image", state.image);
   
    dispatch(createStore(form_data))

  };

    return (
      <>
        <Row style={{ backgroundColor:'#1e478a', height:'3rem' }}>
        <Nav variant="pills" className="d-flex justify-content-center">
          <Nav.Item>
            <Nav.Link className='d-flex align-items-center' style={{ height:'2rem', borderRadius: '30px 30px 30px 30px' }} onClick={() => setSelect(!select)} eventKey="product">Add Store</Nav.Link>
          </Nav.Item>
        </Nav>
        </Row>
      { select && (
        navigate('add-store')
      )}
    </>
    );
  }

  const Wrapper = styled.div`
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  margin: 0 auto;
`;

const Input = styled.input`
  width: 400px;
  background: white;
  border: none;
  padding: 10px 20px;
  border-radius: 30px;
  position: relative;
  display: grid;
  justify-self: center;
  &:focus {
    outline: none;
    border-radius: ${(props) => props.isTyping && "10px 10px 0px 0px"};
  }
`;

const SuggestionWrapper = styled.div`
  background: white;
  position: absolute;
  width: 400px;
  padding: 10px 20px;
  border-radius: 0px 0px 10px 10px;
`;

const Suggestion = styled.p`
  cursor: pointer;
  max-width: 400px;
`;