import { NEW_STORE_FORM } from "../types";

const initialState = {
  categories: ''
};

export const newStoreFormReducer = (state = initialState, action) => {
  switch (action.type) {
    case NEW_STORE_FORM:
      return {
        ...state,
        categories: action.payload
      };

    default:
      return state;
  }
};
