import { GET_ALL_STORES } from "../types";

const initialState = {
  stores: []
};

export const storesReducer = (state = initialState, action) => {
  switch (action.type) {
    case GET_ALL_STORES:
      return {
        ...state,
        stores: action.payload
      };

    // case NEW_ALBUM: {
    //   return {
    //     ...state,
    //     albums: action.payload
    //   };
    // }


    // case SEARCH: {
    //   return {
    //     ...state,
    //     albums: action.payload
    //   };
    // }


    default:
      return state;
  }
};
