import React, { useEffect, useState } from 'react'
import { NavLink, Card, Row, Carousel, Col, ListGroup, Form, Button, Image, Container } from 'react-bootstrap';
import { useDispatch } from 'react-redux'
import { search } from '../store/actions/searchAction'
import { listMyStores } from '../store/actions/storeActions';

export default function SearchBox({ type, placeholder, color, value, setValue }) {

    const dispatch = useDispatch()

    const searchHandler = (e) => {
        setValue(e.target.value)
        dispatch(search({ type: type, searchString: e.target.value }))
    }
    
  return (
    <Row style={{ backgroundColor: color, height:'3rem' }}>
        <Form className="d-flex justify-content-center my-2" style={{ height:'2rem' }}>
            <Form.Control
            type="search"
            placeholder={placeholder}
            aria-label="Search"
            style={{width: '50%', borderRadius: '30px 30px 30px 30px' }}
            value={value}
            onChange={(e) => searchHandler(e)}
            />
        </Form>
    </Row>
  )
}