import { SET_TOKEN } from "../types";
import { LOG_OUT } from "../types";

const initialState = {
  token: null,
  authenticated: null
};

export const tokenReducer = (state = initialState, action) => {

  switch (action.type) {
    case SET_TOKEN:
      return { ...state, token: action.payload, authenticated: true };

    case LOG_OUT: {
      return { token: null, authenticated: false };
    }

    default:
      return state;
  }
};

