import {
    CART_ADD_ITEM,
    CART_REMOVE_ITEM,
    CART_SAVE_SHIPPING_ADDRESS,

    CART_SAVE_PAYMENT_METHOD,

    CART_CLEAR_ITEMS,
} from '../constants/cartConstants'



export const cartReducer = (state = { cartItems: [], shippingAddress: {} }, action) => {
    switch (action.type) {
        case CART_ADD_ITEM:
            const newProduct = action.payload
            const isProductInCart = state.cartItems.find(cartProduct => cartProduct.id === newProduct.id)

            if (isProductInCart) {
                return {
                    ...state,
                    cartItems: state.cartItems.map(cartProduct =>
                        cartProduct.id === isProductInCart.id ? newProduct : cartProduct)
                }

            } else {
                return {
                    ...state,
                    cartItems: [...state.cartItems, newProduct]
                }
            }

        case CART_REMOVE_ITEM:
            return {
                ...state,
                cartItems: state.cartItems.filter(x => x.id !== action.payload)
            }

        case CART_SAVE_SHIPPING_ADDRESS:
            return {
                ...state,
                shippingAddress: action.payload
            }

        case CART_SAVE_PAYMENT_METHOD:
            return {
                ...state,
                paymentMethod: action.payload
            }

        case CART_CLEAR_ITEMS:
            return {
                ...state,
                cartItems: []
            }

        default:
            return state
    }
}