import React, { useEffect, useState } from 'react'
import { NavLink, Nav, Card, Row, Carousel, Col, ListGroup, Form, Button, Image, Container } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import Popup from 'reactjs-popup';
import './menu.css'
import Rating from '../components/Rating'
import { useDispatch, useSelector } from 'react-redux'
import { listProductCategories } from '../store/actions/categoriesActions'
import { listProducts, listReviews, listLatestProducts } from '../store/actions/productActions'
import { search } from '../store/actions/searchAction'
import { listStores } from '../store/actions/storeActions'
import { listUsers, listProfiles } from '../store/actions/userActions'
import { CFormCheck } from '@coreui/react';
import Reviews from '../components/Reviews';
import SearchBox from '../components/SearchBox';

export default function HomeScreen() {

    const [value, setValue] = useState('')
    const [showResult, setShowResult] = useState(false)
    const [mainSearchValue, setMainSearchValue] = useState('')

    const dispatch = useDispatch()
    const { categories } = useSelector(state => state.productCategories)
    const { products } = useSelector(state => state.productList)
    const { stores } = useSelector(state => state.storeList)
    const { users } = useSelector(state => state.userList)
    const { profiles } = useSelector(state => state.profileList)
    const { latestProducts } = useSelector(state => state.latestProductsList)

    useEffect(() => {
        dispatch(listProductCategories())
        dispatch(listProducts())
        dispatch(listLatestProducts())
        dispatch(listReviews())
        dispatch(listStores())
        dispatch(listUsers())
        dispatch(listProfiles())
    }, [dispatch])

    const categoryFilterHandler = (keyword) => {
        setShowResult(true)
        dispatch(search({ type: 'products', searchString: keyword }))
    }

  return (
    <>
        < SearchBox value={value} setValue={setValue} type='all' placeholder='Search for a product, brand or store..' color='#1e478a'/>
        <Nav className='justify-content-evenly'>
            {categories.map((category)=>{
            return (
                  <Nav.Item key={category.id}>
                    <Nav.Link onClick={()=>{categoryFilterHandler(category.name)}}>{category.name}</Nav.Link>
                  </Nav.Item>
            )
        })}
        </Nav>
        {
        showResult && (
            <Button onClick={() => setShowResult(false)} color='#1e478a' className='mx-2'>Back</Button>
        )
        }
        <Row >
        {
            (!showResult && value === '') && (
                <Row >
        < Reviews />
        <Col md={6}>
         <Carousel pause='hover' style={{ backgroundColor:'#1e478a' }} className='text-center main-carousel'>
                    {latestProducts.map((product) => {
                        return (
                        <Carousel.Item key={product.id}>
                            <Link to={`/product/${product.id}`}>
                                    <h4>{product.brand}</h4>
                                    <h5>{product.name}</h5>
                                <Image className='main-carousel-img' src={product.image} alt={product.name} fluid /> 
                                <h5>CHF {product.price}</h5>
                            </Link>
                        </Carousel.Item>
                        )
                        })}
                </Carousel>
        </Col>
        <Col md={3} style={{ padding:'2rem' }}>
            <h3>Latest Stores</h3>
            <p>Some dummy text to pretend a user has added a store. Which is not true since I haven't added that feature yet. Not even a store option at all.</p>
        </Col>
        
        </Row>
            )
        }
            <Row className='my-2'>
                    <Col lg={2} xl={2} className='m-4'>
                        {categories.map((category) => {
                            return <>
                                <h5 className='my-3' style={{ color:'#1e478a' }} key={category.id}>{category.name}</h5>
                                {category.subcategories.map((subcategory) => {
                                    return <CFormCheck 
                                    type="radio" 
                                    name="flexRadioDefault" 
                                    id="flexRadioDefault1" 
                                    label={subcategory.name}
                                    style={{ backgroundColor:'#1e478a' }}
                                    onChange={() => {categoryFilterHandler(subcategory.name)}}
                                    className='my-2'
                                    key={subcategory.id}
                                    />
                                })}
                                </>      
                        })}
                    </Col>
                    <Col>
                        <Row>
                            {products && products.map((product) => {
                                const seller = profiles && profiles.find(profile => profile.name === product.seller)
                            return (
                                    <Col sm={12} md={6} lg={3} className='gx-3 gy-2'>
                                    <Card className='main-product-card' key={product.id} style={{ padding:'0.2rem 0.2rem 0rem 0.2rem'}} >
                                        <Link to={`/product/${product.id}`}>
                                            <Card.Img src={product.image} variant='top' className='card-img-top'/>
                                        </Link>
                                        <Card.Body>
                                            <Link to={`/product/${product.id}`}>
                                            <Card.Title className='text-center'>
                                                    <strong>{product.brand}</strong>
                                                </Card.Title>
                                                <Card.Title className='text-center'>
                                                    <strong>{product.name}</strong>
                                                </Card.Title>
                                            </Link>
                                            <p style={{ fontSize:'1rem'}}>{product.description}</p>
                                            {/* <Card.Text as="div">
                                                <div className="my-3">
                                                    <Rating value={product.rating} text={`${product.numReviews} reviews`} color={'#f8e825'} />
                                                </div>
                                            </Card.Text> */}
                                            <Card.Text as="h5" className='text-center'>
                                                CHF {product.price}
                                            </Card.Text>
                                            <p style={{ fontSize:'0.8rem'}} className='text-center'>Sold by <Link to={`/retailers/${seller && seller.id}`}>
                                            {seller && seller.name}
                                            </Link>
                                            </p>
                                        </Card.Body>
                                    </Card> 
                                    </Col>
                            )
                                
                                        })} 
                        </Row>
                    </Col>
            </Row>  
    </Row>
    
    </>
  );
}